@import url('https://fonts.googleapis.com/css?family=Amatic+SC&display=swap');

body {
  margin: 100px;
  
}
.App {
  display: flex;
  justify-content: center;
  font-family: 'Amatic SC', cursive;
  font-size: 40px;
  padding: 10px;
  background-color: rgb(248, 253, 255);
  border: 2px solid lightblue;
  color: rgb(108, 128, 151);
  
}

.App h1 {
  display: inline-block;
}

.check {
  font-size: 30px;
}
.input {
  font-family: 'Amatic SC', cursive;
  font-size: 30px;
  display: inline-block;
  margin-left: 60px;
  margin-right: 20px;
  color: rgb(108, 128, 151);
}

button {
  font-family: 'Amatic SC', cursive;
  margin: 0px;
  padding: 10px;
  border-radius: 50%;
  font-size: 25px;
  align-items: center;
  color: white;
  background-color: #ffccff;
}

button:hover {
  color: black;
  background-color: #660066;
}
.list {
  margin-right: 10px;
  color: rgb(108, 128, 151);
  margin-left: 60px;
  margin-right: 60px;
  list-style-type: disc;
}